.articlepage .articlebanner {
  height: 400px
}
.articleinfo {
  padding: 20px 5%;
  min-height: 50vh
}
.articleinfo p {
  color: #777777
}
.articlepage .banner  > img {
  filter: brightness(50%)
}
.anchorlinks {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding:  20px 0;
  font-size: 14px;
  position: sticky;
  top: 80px;
}
.anchorlinkscont {
  position: relative;
  top: 0;
}
.anchorlinks a::before {
  content: '';
  width: 8px;
  height: 8px;
  position: absolute;
  left: -15px;
  top: 50%;
  transform: scale(.6) translateY(-50%);
  background-color: var(--second-color);
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s;
}
.anchorlinks .activeanchor::before {
  opacity: 1;
  transform: translateY(-50%) scale(1)
}
.articlecontpage {
  width: 550px;
  max-width: 90%;
  margin: 40px auto;
  margin-top: 80px;
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-gap: 5px

}
.articlecontpage .paragraph {
  margin: 10px 0;
  scroll-margin-top: 100px;
}

.articlecontpage h2 {
  margin: 10px 0;
  font-size: 20px
}
.paragraphimg {
  width: 100%;
  margin: 15px 0
}
.paragraphimg img, .paragraphimg .imgskeltonload {
    width: 100%;
    border-radius: 5px;
    height: 350px;
    max-height: 350px
}

.paragraphimg small {
  position: absolute;
  bottom: 0;
  right: 0px;
  padding: 10px;
  background-color: #fff;
  border-radius: 5px 0;
  font-size: 12px;
  width: auto;
  max-width: 50%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.articleinfobanner {
  position: absolute;
  top: 35%;
  color: #fff;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px
}
.articleinfobanner .by, .info {
  display: flex;
  align-items: center;
  gap: 10px;
}
.by a {
  color: #fff
}
.by a:hover {
  text-decoration: underline
}
.info span:last-of-type {
  text-transform: capitalize
}
.body .articleinfobanner .by img {
  width: 35px;
  height: 35px;
  border-radius: 50%
}
.articleinfobanner h2 {
  font-size: 50px
}
.categories {
  width: 550px;
  max-width: 90%;
  margin: 0 auto;
  display: flex;
  gap: 10px
}
.categories div {
  padding: 5px 15px;
  font-size: 12px;
  box-shadow: var(--light-shadow);
  border-radius: 3px;
  transition: all 0.3s
}
.categories div:hover {
  color: var(--second-color)
}