
.spacer {
  height: 100vh
}
.storyrow {
  background-color: #f9f9f9;

}
.storytext {
  display: flex;
  max-width: 90%;
  width: 550px
}
.storytext .iconbox {
  box-shadow: none;
  background-color: transparent;

}
.ourstoryiconbox .innericonboxcontainer{
  width: 250px;
  height: 250px;
}
.storyrow .innercontainerrow {
  display: flex;
  flex-wrap: wrap;
  gap: 60px
}
.storytext > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5px
}
.ourteam {
  background-color: #f9f9f9;
  padding: 40px 0
}
.titlecont .activefilter {
  color: var(--theme-color)
}
.ourteam table {
  width: 800px;
  max-width: 80%;
  border: solid 1px #ddd;
  padding: 20px
}
.ourteam  {
  display: flex;
  justify-content: center;
  height: auto;
  flex-wrap: wrap;
  gap: 30px
}
.griditem {
  width: 300px;
  height: 300px;
  display: flex;
  border-radius: 5px;
  box-shadow: var(--light-shadow);
  overflow: hidden;
}
.griditem:first-of-type {
  justify-content: center;
  align-items: center
}
.ourteam .teamimg {
  width: 100%;
  height: 100%;

}
.griditem:hover .teamdescription {
  opacity: 1;
  transform: scale(0.85)

}
.teamimg img {
  width: 100%;
  height: 100%
}
.ourteam .teamdescription {
  width: 100%;
  position: absolute;
  padding: 20px;
  background-color: rgba(240, 240, 240, 0.795);
  height: 100%;
  border-radius: 5px;
  opacity: 0;
  transition: all 0.3s;
  transform: scale(0.7);
  overflow-y: scroll
}
.teamdescription::-webkit-scrollbar {
  width: 0
}
.teamdescription p {
  margin-top: 20px
}
.contactinfo {
  display: flex;
  gap: 10px;
  margin: 5px 0
}
.contactinfo i {
  width: 40px;
  height: 40px;
  background: var(--theme-gradient);
  display: flex;
  justify-content: center;
  align-items: center;
  background-position: center;
  background-size: cover;
  color: #fff;
  border-radius:50%

}
.ourstory > .titlecont {
  background-color: #f9f9f9;
}
.ourstory .titlecont h1 small span {
  margin: 0 10px;
  cursor: pointer;
}
.ourstory .member {
  width: 200px;
  height: auto;
}

.ourstory .member .memberimg {
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 5px
}
.ourstory .member a:hover {
  text-decoration: underline
}
.memberimg img {
  width: 100%;
  height: 100%
}
.membertype {
  text-align: center;
  margin-top: 10px
}
.membertype span {
  color: var(--gray-text);
  font-size: 12px
}
.ourstory .containerrow .titlecont span::after, .ourstory .containerrow + .titlecont span::after {
  background-color: #000
}
.ourmission {
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
  gap: 30px
}
.ourmission .step {
  padding: 0
}
.ourmission  .innerstep{
  min-height: 190px;
  width: 250px;
  display: flex;
  flex-direction: column;
  transition: all 0.3s;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff
}
.ourmission .stepcont {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}
.ourmission .stepcont .steptitle {
  color: var(--second-color)
}
.ourmission .stepcont span {
  color: var(--gray-text)
}
.ourmission .step {
  background-color: transparent
}
.ourmission   .innerstep:hover {
  background-color: var(--second-color);
  transform: translateY(-10px);
  box-shadow: 0px 22px 36px -19px var(--second-color);
}
.ourmission   .innerstep:hover .stepcont span {
  color: #fff 
}
.ourmission .step .stepnum {
  transition: all 0.3s
}
.ourmission  .step:hover .stepnum {
  background-color: #fff;
  color: var(--second-color)
}
@media (max-width: 550px) {
  .ourmission {
    display: block;
  }
  .ourmission > div {
    margin: 0 auto;
    margin-top: 30px;

  }
}