.inneracademy {
  gap: 20px;
  display: flex;
  padding: 20px;
  margin-top: 50px;
  border-radius: 5px;
  flex-direction: column;
  max-width: 750px;
}
.academy {
  width: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 0;
  background-color: transparent;
  height: 100%;
  align-items: center
}
.academy h1 {
  color: #fff;
  font-size: 35px
}
.academyleft {
  display: flex;
  align-items: center;
  color: #fff
}
.academyright button {
  width: 100%;
} 
.academyright {
  display: flex;
  flex-direction: column;
  gap: 20px;
  color: #fff;
  max-width: 70%;
}
.gitbook h1 {
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
  align-items: center
}
.gitbook i {
  color: #fff
}
.gitbook h1 > span:last-of-type {
  display : flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px
}
.elipcont {
  display: flex;
  flex-direction: column;
  gap: 7px;
  cursor: pointer;
}
.elipcont .elip {
  width: 6px;
  height: 6px;
  display: block;
  background-color: #fff;
  border-radius: 50%
}
@media (max-width: 800px) {
  .academyright {
    max-width: initial;
  }
}