.column {
  display: flex;
  flex-direction: column;
}

.column .col a {
  margin: 10px 0;
  font-size: 13px
}
.column:hover span,   .column:hover h3  i {
  color: var(--second-color)
}
.column a:hover {
  text-decoration: underline
}
.column div a:hover {
  color: var(--theme-color);
  text-decoration: underline
} 
  .fa-cc-visa {
  color: #1A1F71
}
 .fa-cc-paypal {
  color: #009cde
}
.fa-cc-mastercard {
  color: #000
}
.fa-cc-amex {
  color: #002663
}
.column  div img {
  width: 30px;
  height: 25px;
  border-radius: 5px;
  margin-top: 10px
}
.column span{
}

.column .col {
  overflow: hidden;

    transition: all 0.3s;

}
.column .activecol {
  transition: all 0.8s;
  max-height: 500px;
}
.column h3 {
  display: flex;
  justify-content: space-between;
  width: 100%;;
}
.column h3 i {
  display: none
}

.col {
  display: flex;
  flex-direction: column
}

@media (max-width: 800px) {
  .footer .logo {
    left: -15px;
  }
  .columns {
    flex-direction: column;
    padding: 20px 10%
  }

  .column .col {
    max-height: 0
  }
  .column .activecol {
    max-height: 500px;;
    margin-top: 20px
  }
  .column h3 {
    cursor: pointer;
    transition: all 0.3s
  }
  .column h3:hover {
    color: var(--theme-color)
  }
  .column .activecoltitle i {
    transform: rotate(90deg)
  }
  .column h3 i {
    display: block
  }
  .column {
    margin: 15px 0
  }
  .column .col {
    padding-left: 20px;
  }
}