
@font-face {
  font-family: 'NeueMontreal';
  src: URL('/src/fonts/NeueMontreal-Regular.ttf') format('truetype');
}
html, body {
  margin: 0;
  padding: 0;
  font-family: 'NeueMontreal'
}
.app {
  min-height: 100vh;
}
:root {
  --theme-gradient:url(https://0xsociety.com/wp-content/uploads/2021/06/gradient-0xSociety-scaled.jpg);
  --second-gradient: linear-gradient(47deg, rgba(0,160,251,1) 0%, rgba(77,86,245,1) 57%, rgba(182,184,188,1) 100%);
  --light: #35b6fa11;
  --light-gray: #726c6c;
  --shadow: 0px 0px 7px 1px rgb(204, 204, 204);
  --light-shadow:  0px 0px 7px 1px rgb(238, 238, 238);
  --theme-color:#000;
  --gray: rgb(251, 251, 251);
  --light-blue: #ebf5ff;
  --theme-light: #37adf8;
  --theme-light-color: #4b86b4;
  --gray-text: #696e93;
  --second-gray:rgb(199, 199, 199); 
  --red: rgb(255, 53, 53);
  --green: rgb(25, 221, 25);
  --primary-bg: #242533;
  --secondary-bg: #323444;
  --inactive-box: #404456;
  --icon-shadow:drop-shadow(0px 10px 5px rgba(1, 203, 152, 0.842));
  --second-color: #047374; 
  --overlay:  rgba(0,0,0,.5);
}
.themeicon {
  transition: all 0.3s
}
#root .green {
  color: var(--green)
}
#root .red {
  color: var(--red)
}

a {
  text-decoration: none;
  color: #000;
  transition: all 0.3s;
}
.app .fa-exclamation-circle {
  color: var(--red);
}
.app .fa-check-circle {
  color: var(--green)
}
.app .fa-exclamation-triangle {
  color: rgb(255, 212, 22)
}
.capitalize {
  text-transform: capitalize
}
.graytext {
  color: var(--gray-text);
}
p,h1,h2,h3,h4,h5,h6,a {
  margin: 0;
  padding: 0;
}* {
  box-sizing: border-box;
  position: relative;
}
img {
  object-fit: cover;
  background-color: #f9f9f9
}
input,
button {
  outline: none;
}
p {
  outline: none;
}
hr {
  border: none;
  background-color: #f0f0f2;
  width: 90%;
  height: 1px;
}
.spacer1 {
  height: 100px
}

i {
  cursor: pointer;
  transition: all 0.3s;
  color: var(--theme-color)
}
textarea::-webkit-scrollbar {
  width: 5px
}
time {
  text-transform:capitalize;
 }
button {
  cursor: pointer;
  border: solid 2px transparent;
  transition: all 0.3s;
}

::selection {
  background-color: #000;
  color: white;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #fff

}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}
input {
  border: none;
  padding: 10px;
  background-color: #f8f8f8  
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.fillcontainer {
  width: 100%;
  height: 100%;
}
.MuiButton-label {
  height: 100%
}
.MuiTouchRipple-root {
  opacity: 0.2;
}
.flexrow {
  display: flex;
  align-items: center;
  justify-content: center
}
.flex {
  display: flex;
  flex-direction: column
}
.titlecont h1 {
  display: flex;
  flex-direction: column;
  text-align: center
}
.placeholderimg {
  background: url(https://0xsociety.com/wp-content/uploads/2021/06/2xztxQ4-1-scaled.jpg);
  background-size: cover;
}
.templatebanner  {
  background-color: #f9f9f9
}
.logofont {
  font-family: 'Inconsolata', monospace;
}
.imgcontainer {
  border-radius: 5px;
  overflow: hidden;
  box-shadow: var(--light-shadow)
}
.rowreverse {
  flex-direction: row-reverse
}
