.article {
  width: 300px;
  height: auto;
  box-shadow: 0px 20px 40px -30px rgb(0 0 0 / 45%);
  border-radius: 5px;
  overflow: hidden;
}
.article img {
  width: 100%;
  transition: all 0.3s;
  height: 100%
}
.articleimg {
  width: 100%;
  height: 225px;
  max-height: 225px;
  overflow: hidden
}
.article .articleimg:hover img{
  transform: scale(1.05)
}
.article .articlecont {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.article button {
  width: 100%
}
.article .appbtn:hover i {
  transform: translateX(20px);
  margin: 0 5px;
  margin-left: 15px
}
.article .readmore {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  align-items: center;
}
.readmore .themeicon {
  margin-right: 5px
}