.learncont > h2 {
  text-align: center
}
.learncont {
  height: auto;
  background-color: #fff;
  padding: 20px
}
.container {
  display: flex;
  width: 550px;
  max-width: 90%;
  height: 350px;
}
.cont {
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
}
.cont p {
  font-size: 14px
}
.contimg {
  width: 200px;
  height: 250px;
  margin: auto 0
}
.container img {
  width: 200px;
  height: 100%;
  border-radius: 5px;
  top: 50%;
  transform: translateY(-50%)
}
.container .iconbox {
  box-shadow: none
}
.container button {
  width: 100%
}
.innerlearncont {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}
.container:nth-of-type(even) {
  flex-direction: row-reverse
}
@media (max-width: 600px) {
  .container {
    flex-direction: column;
    height: auto;
    align-items: center
  }
  .container:nth-of-type(even) {
    flex-direction: column
  }
}