.artistinfo {
  display: flex;
  margin: 90px;
  justify-content: center;
  gap: 20px 
}

.artistpage .banner {
  position: sticky;
  top: 0;
  display: flex;
}
.artistpage .banner .bannerimgcont{
  width: 100%
}
.artistpage .banner img {
  box-shadow: var(--shadow);
  transition:  all 0.3s;
  width: 100%;
  display: block
}
.artistinfo .artinfoimg {
  width: 100%;
  height: 400px;
  max-width: 300px;
}
.artinfoimg img {
  width: 100%;
  height: 100%;
}
.artistinfo .artistcont {
  width: 100%;
}
.artistinfo div {
  width: 100%
}
.artistpage .text {
  padding: 20px;  
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(199, 199, 199);
  color: #fff;
  transition:  all 0.3s;
  max-width: 450px;
  width: 100%
}
.artistpage .text a, .artistpage .text a i {
  color: #fff
}
.text a {
  display: block;
  margin-top: 10px;
  color: var(--second-color);
}
.text a:hover span {
  text-decoration: underline
}
.text a i {
  color: var(--second-color)
}
.text .artistdecrip {
  margin: 10px 0;
  display: block
}
