.skillscont {
  padding-bottom: 20px;
  background-color: #f9f9f9
}

.skillcol {
  display: flex;
  height: 60vh;
  width: 800px;
  max-width: 90%;
  gap: 20px;
  justify-content: center;
  align-items: center
}
.skillcol:nth-of-type(even) {
  flex-direction: row-reverse
}
.skillcol .skillbox  .iconbox {
  height: 50px;
  padding: 0
}
.skillcol .skillbox {
  box-shadow: none;
  margin-top: 20px
}
.skillcol .skillinfo {
  padding: 20px;
  border-radius: 5px;
  width: 50%;
  background-color: #eee;
}
.spacercol {
  width: 100%;
  overflow: hidden;
  border-radius: 5px;

}
.artists .carousel .slider-wrapper.axis-horizontal .slider, .artists .carousel .slider-wrapper, .artists .carousel .slider-wrapper.axis-horizontal .slider .slide  {
  height: auto;
  min-height: 100vh
}
.spacercol img {
  width: 100%;
  height: 100%;
  transition: all 0.3s;
  cursor: crosshair;
}
.spacercol:hover img {
  transform: scale(1.1)
}

.slider-wrapper i {
  color: var(--second-color)
}
.skillscont h2 {
  margin: 40px 0
}
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  /* margin-top: 25px; */
  gap: 25px;
  background-color: #f9f9f9;
  padding-top: 40px
}
.artistpage .works {

}
.artistquotes {
  height: 40vh;
  background-color: #eee;

}
.quote {
  display: flex;
  flex-direction: column
}
.quote i{
  font-size: 26px;
  align-self: flex-start;
  margin-bottom: 10px
}
.quote .skeletonquote {
  width: 340px;
  max-width: 90%;
  background-color: #fff;
  animation: skeleton 2.2s infinite;
  height: 30px;
  display: block; 
  border-radius: 5px
}
@keyframes skeleton{
  0% {
    opacity: 1;
  }
50% {
  opacity: 0.6;
}
100% {
  opacity: 1;
}
  
}
.carousel .slide {
  display: flex;
    justify-content: center;
    align-items: center;
}
.artistpage .carouselbtn {
  color: var(--second-color);
}
.artistpage .carouselbtn {

  top: 46%;
}
.artistpage .carouselbtn:first-of-type {
  top: 54%;
}
#root .artistpage .carousel li {
  color: #000
}
#root .artistpage .carousel .selectedbtn {
  color: var(--second-color)
}
