@media (max-width: 800px) {
 .skillscont .skillcol {
   flex-direction: column;
   height: auto;

 }
 .skillcol .skillinfo {
  width: 400px;
  max-width: 90%;
 }
 .spacercol {
   width: 400px;
   max-width: 90%;
 }
}

@media (max-width: 750px) {
  .artistinfo {
    flex-direction: column
  }
  .artistinfo img {
    width: 100%;
    max-width: initial;
  }
}
@media (max-width: 630px) {
  .skillcol .skillinfo {
    width: 100%;
    margin: 20px 0
  }
}
@media (max-width: 400px) {
  .text h2 {
    font-size: 18px;
  }
  .text span {
    font-size: 15px
  }
}