.body .contactbanner {
  height: 60vh;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
}
.contactbanner img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;

  
}
.contactbanner h2 {
  z-index: 1;
  text-align: center;
  color: #fff;
  transition: all 0.3s
}
.contactbanner .opacity {
  opacity: 0;
}
.contact .titlecont {
  z-index: 1;
  color: #fff
}
.contact .titlecont span::after {
  background-color: #fff
}
.contact .contactboxes {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 50px 0;
  padding: 10px;
  z-index: 1;
}
.contact .iconcont {

  box-shadow: var(--light-shadow);
  background-color: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 15px;
  width: 170px;
  height: 170px
}
.contact .iconcont .iconcon {
  position: relative;
  top: initial;
  box-shadow: none;
  font-size: 25px
}
.contact .iconcont a {
  font-size: 11px
}
.contact textarea {
  min-height: 100px
}
.sendamessage {
  display: grid;
  grid-template-columns: 1fr ;
  grid-gap: 20px;
  width:  650px;
  max-width: 90%;
  margin: 0 auto;
  margin-top: 30px
}
.leftpart i{
  font-size: 28px
}

.leftpart, .rightpart {
  display: flex;
  justify-content: center;
  align-items: center
}
.leftpart {
  gap: 10px;
  flex-direction: column;
 align-items: flex-start
}
.rightpart {
  flex-direction: column;
  gap: 10px;
}

.rightpart span {
  color: #000;
  font-size: 12px
}
.rightpart label {
  width: 100%
}
.rightpart input, .rightpart textarea, .rightpart select {
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  border-radius: 30px
}
.rightpart textarea {
  border-radius: 10px
}
.rightpart {
  color: #fff
}
.rightpart button span {
  color: #fff
}
.rightpart textarea {
  background-color: #f9f9f9;
  resize: vertical
}
textarea {
  outline: none;
  border: none
}
.rightpart button {
  width: 100%;
}
.rightpart form {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 15px
}
select {
  padding: 10px;
  outline: none;
  border: none;
  background-color: #f8f8f8;
}
.files input {
  border: none;
  outline: none;
  display: none
}
.files {
  margin-top: 20px;
}
.files .innerfilescont {
  display: flex;
  background-color: #f8f8f8;
  color: #000;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  border-radius: 40px
}
.files .appbtn {
    width: 200px;
    max-width: 100%;
    margin: 0;
    cursor: pointer;
}