.modal {
  position: fixed;
  top: -100px;
  background-color: var(--overlay);
  left: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  opacity: 0;
  transition: all 0.3s
}
.modal-enter-done {
  top: 0;
  opacity: 1;
}
.closemodal {
  position: absolute;
  top: 30px;
  right: 30px;
  font-size: 40px;
  color: #fff
}
