.navlink {
  color: #fff
}
.navlink::before {
  position: absolute;
  bottom: -30px;
  width: 100%;
  height: 30px;
  content: ''
}
.navlink:hover, .activenavbarlink {

}

.navlink::after {
  position: absolute;
  bottom: -6px;
  width: 0%;
  height: 2px;
  content: '';
  display: block;
  left: 0;
  transition: all 0.3s;
  background-color: #fff
}
.scrollednav .navlink::after {
  background-color: #000
}
.navlink:hover::after, .activenavbarlink::after {
  width: 60%
}