.teammemberbanner {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url(https://0xsociety.com/wp-content/uploads/2021/06/sLSSe2c-scaled.jpg);
  background-size: cover;
  background-position: center;
  padding: 90px 0
}
.teammemberbanner .teammemberimg {
  width: 280px;
  height: 280px;
  overflow: hidden;
  padding: 10px;
  border-radius: 50%;
}
.teammemberbanner .teammemberimg img {
  width: 100%;
  height: 100%;
  border-radius: 50%;

}
.teammemberbanner .teammembercont {
  width: 70%;
  max-width: 400px;
  color: #fff;
  display: flex;
  flex-direction:column;
  gap: 10px
}
.contacticons {
  z-index: 0;
  position: fixed;
  bottom: 40px;
  right: 40px;
  display: flex;
  gap: 20px
}
.contacticons i {
  color: #fff
}