.carousel-root, .carousel, .slider-wrapper, .carousel .slider  {
  height: 100%;
  max-height: 100%;
  background-color: inherit
}
.carousel .slider  {
  overflow: initial;
  padding: initial;
  height: initial;
}
.carousel .slide {
  height: 100%
}
.carousel .slider {
  height: 100%
}
.body .carousel .control-dots {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 0;
  position: absolute;
  top: 50%;
  width: auto;
  flex-direction: column;
  transform: translateY(-50%)
}
.control-dots li {
  list-style: none
}
.body .carousel-status {
  display: none
}
#root .carousel .control-arrow {
  position: absolute;
  right: 0;
  left: initial
}
#root .carousel .control-arrow:first-of-type {
  top: 10px
}
.carouselbtn {
  position: absolute;
  right: 25px;
  top: 48%;
  z-index: 1;
  transform: translateY(-50%);
  font-size: 28px; 
  color: #fff;
  cursor: pointer;
}
.carouselbtn:first-of-type {
  top: 52%;
}
#root .thumbs-wrapper {
  display: none
}
#root .sl  {
  height: auto;
}
.carousel * {
  box-shadow: none
}
