.eventcard {
  width: 280px;
  height: 420px;
  box-shadow: 0px 20px 40px -30px rgb(0 0 0 / 45%)
}
.eventcard .eventimgcont {
  width: 100%;
  height: 60%;
  overflow: hidden;
  border-radius: 5px;
  cursor: crosshair
}
.eventcard .eventimgcont:hover img {
  transform: scale(1.1)
}
.eventcard .eventdate {
  font-size: 12px
}
.eventcard .eventimgcont img {
  width: 100%;
  height: 100%;

  transition: all 0.3s
}
.eventsrow {
  background-color: #f9f9f9
}
.eventsrow .innercontainerrow{
  flex-wrap: wrap;
  gap: 30px
}
.eventcont {
  background-color: #fff;
  padding:   20px;
  border-radius: 0 0 5px 5px;
  height: 40%;
  justify-content: center;
}
.eventcontmodal .eventcont {
  border-radius: 5px;
  height: 100%

}
.eventcont {
  display: flex;
  flex-direction: column;
  gap: 10px
}
.eventstitle {
  display: flex;
  flex-direction: column
}
.filtersrow {
  display: flex;
  justify-content: center;
  gap: 10px
}
.filtersrow span {
  font-size: 13px;
  margin-top: 20px;
  cursor: pointer;
}
.filtersrow .activefilter {
  color: var(--theme-color)
}
.innereventtitle {
  display: flex;
  justify-content: space-between;
  gap: 10px
}
.innereventtitle > div {
  width: 100%
}
.search {
  display: flex;
  align-items: center;
  justify-content: center
}
.search i {
    position: absolute;
    z-index: 1;
    font-size: 14px;
    top: 50%;
    transform: translateY(-50%);
    left: 10px
}
.search label {
  height: 100%;
  display: block;
  height: 36px;
}
.search input {
  padding-left: 30px;
  background-color: #fff
}
.eventcontmodal {
  width: 90%;
  max-width: 650px;
  height: 60vh;
}
.descriptionevent {
  max-height: 50vh;
  overflow-y: auto
}

.descriptionevent::-webkit-scrollbar {
  width: 5px
}
@media (max-width: 700px) {
  .innereventtitle div:first-of-type {
    display: none
  }
}
@media (max-width: 450px) {
  .innereventtitle {
    flex-direction: column;
  }
}