.benefits {
  display: flex;
  flex-direction: column
}
.benefits h2 {
  text-align: center;
  margin-top: 40px
}
.innerbenefits {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
  margin: 40px 0;
  background-color: #f9f9f9;
  padding: 20px
}
.membergrid {
  display: flex;
  justify-content: center;
}
.placeholderimg {
  width: 600px;
  height: 55vh;
  background-color: #f9f9f9;
  margin: 20px 0;
  border-radius: 10px;
  max-width: 90%;
}

.memberbaner {
  height: auto
}
.benefits {
  background-color: #fff
}
.benefits .iconbox {
  box-shadow: none;
  background-color: transparent
}
.steps {
  padding: 40px 20%;
  overflow-x: hidden;
}
.steps h2 {
  text-align: center;
  margin-bottom: 40px
}
.innersteps {
  display: flex;
  flex-direction: column;
  gap: 60px;
}
 .step {
  width: 50%;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
}
.step p {
  font-size: 14px;
  margin-top: 5px
}
.stepnum {
  position: absolute;
  top: -20px;
  background-color: var(--second-color);
  width: 35px;
  height: 35px;
  color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center
}
.step:nth-of-type(odd) {
  align-self: flex-end
}
.newsletter {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  padding: 50px 0;
  background-color: #fff
}
.newsletter span {
  text-align: center
}
.newsletter form{
  width: 550px;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  gap: 10px
}
.newsletter form span {
  text-align: left;
  font-size: 12px;
}

.newsletter .controls {
  display: flex;
}
.newsletter form input {
  width: 100%;
  border-radius: 30px 
}
.loader {
  height: 40px;
  width: 40px;
  background-color: #000
}
.successmsg {
  opacity: 0;
  transition: all 0.3s;
  color: var(--green);
  pointer-events: none;
}
.success {
  opacity: 1;
}
.loader img {
  width: 100%;
  height: 100%;
}
@media (max-width: 800px) {
  .steps {
    padding: 40px 10px
  }
}