.body .artworkbanner {
  padding: 0;
  height: 100vh;
  width: 100%
}
.artworkbanner .bgimgcont {
  filter: brightness(50%);
  height: 100%;
  width: 100%
}
.artworkbanner img {
  width: 100%;
  height: 100%
}
.artworkbanner .imgcont {
  position: absolute;
  top: 0;
  z-index: 1;
  height: 250px;
  top: 0;
  max-height: 80vh;
  max-width: 90%;
  right: 20%;
  display: flex;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
}
.artworkbanner .imgcont img {
  width: 350px;
  border-radius: 5px;
  max-width: 90%;
  height: 450px
}
