.artworkdetails {
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: center;
  font-size: 24px;
  width: 600px;
  max-width: 80%;
  margin: 20px auto
}
.artworkdetails span {
  font-size: 16px;

}
.artworkdetails a {
  color: var(--second-color);
  text-decoration: underline;
  margin-left: 5px
}
.artworkdetails p {
  font-size: 16px
}
.featuredimgs {
  display: flex;
  flex-direction: column;
  gap: 40px;
  align-items: center;
  margin: 50px 0;
  overflow-x: hidden
}
.featuredimgs .featuredimg {
  width: 800px;
  display: flex;
  height: 500px;
  max-width: 90%;
  gap: 20px
}
.featuredimg:nth-of-type(odd) {
  flex-direction: row-reverse
}
.featuredimgs .featuredimgcont {
  height: 100%;
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  min-width: 50%;
  cursor: crosshair
}
.featuredimgcont:hover img {
  transform: scale(1.1);
  filter: brightness(80%)
}

.featuredimgcont:hover .holdtozoom {
  opacity: 1;
}
.featuredimgs img {
  width: 100%;
  height: 100%;
  transition: all 0.3s;
  display: block;
}
.featuredimgs .featuredcont {
  height: 100%;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 50%;
}
.innerfcont {
  padding: 20px;
  background-color: #f9f9f9
}
.innerfcont h1 {
  margin-bottom: 10px
}

@media (max-width: 900px) {
  .artworkbanner .imgcont {
    right: 5%
  }
}
@media (max-width: 800px) {
  .featuredimgs .featuredimg {
    flex-direction: column;
    height: auto;
    width: 400px;
    max-width: 90%;
  }
  .featuredimg img {
    height: 450px
  }
}
@media (max-width: 700px) {
  .innerfcont h1 {
    font-size: 20px
  }
  .innerfcont span {
    font-size: 14px
  }
}