.scrolltotop {
  position: fixed;
  bottom: 0px;
  right: 20px;
  width: 40px;
  height: 40px;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.74);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
}
.scrolltotop.activescroll {
  bottom: 20px;
  opacity: 1;
  transition: all 0.3s
}
.body .herobanner {
  padding: 0;
}
.body .herobanner img {
  width: 100%;
  height: 100%;
}
.academyinfo, .partners, .exhibitions  {
  background-color: #f9f9f9;
  overflow-x: hidden;
}
.academyinfo {
  padding: 50px 0
}
.inneracademyinfo, .innerexhibition {
  width: 700px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0 auto;
  padding: 20px;
  align-items: center
}
.innerpartners  {
  gap: 80px;
  margin: 40px auto
}
.inneracademyinfo a button {
  width: auto
}

.innerexhibition {
  width: auto;
  gap: 40px
}


.accessrow .iconbox {
  box-shadow: none;
  background-color: transparent;
}
.partners div p {
  margin: 10px 0
}
.partners a i {
  color: var(--second-color);
  margin-right: 5px
}
.innerexhibition {}
.academypage .thb .titlecont h1 {
  justify-content: center;
  align-items: center
}
.thb .titlecont h1 #logo img {
  filter: initial;
  width: 220px;
  height: 80px;
  left: initial;
  position: relative;
  top: initial
}

.academypage .titlecont span::after {
  display: none
}
.innerpartnercont {
  display: flex;
}
.inneracademyinfo a button {
  width: 160px;
  height: 40px;
  border-radius: 30px;

}

.acadmybtn {
  width: 170px;
  height: 40px;
  background-color: transparent;
}
.mr {
  margin-right: 5px
}
.immg, .partner .imgskeltonload  {
  min-width: 250px;
  height: 400px;
  border-radius: 5px;
  max-width: 250px;
}
.partner {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px
}
.inneracadmysteps {
  display: flex;
  gap: 40px;
  margin: 20px 0;
  flex-wrap: wrap
}
.inneracadmysteps > p {
  width: 300px;
  max-width: 100%;
}
.academyinfo .inneracademyinfo h2 {
  padding: 20px;
  background-color: #fff;
  border-radius: 5px
}
.stepnum i {
  color: #fff
}
.partner:nth-of-type(even) {
  flex-direction: row-reverse
}
.contpartner a:hover {
  color: var(--second-color);
}
.noborder .titlecont span::after {
  display: none;
}
@media (max-width: 670px) {
  .inneracadmysteps > p {
    width: 100%;
    min-height: 120px
  }
}
@media (max-width: 500px) {
  .partner, .partner:nth-of-type(even)  {
    flex-direction: column;
    gap: 30px
  }
}