.banner  {
  width: 100%;
  height: 100vh;
  padding: 90px 10%;

}
.hero {
  overflow: hidden;

}
.home {
  scroll-behavior: smooth
}
.hero h1, .hero h2 {
  text-align: center;
  margin: 20px 0;
  font-size: 30px;
  color: #fff;
}
.hero h1 {
  top: 15%
}
.hero img {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.typeonload {
  font-size: 40px;
  color: #fff;
}
.bgwhite {
  background: url(https://0xsociety.com/wp-content/uploads/2021/06/gradient-0xSociety-scaled.jpg); 
  padding: 100px 20%;
  display: flex;
  flex-direction: column;
  gap: 30px;
  height: auto;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
}

.bgwhite h1 {
  color: #fff;
}
.bgwhite p {
  color: #fff;
  text-align: left;
  margin: 20px 0;
  display: flex;
  flex-direction: column
}
.bgwhite p i {
  color: #fff;
  width: 13px;
  margin-top: 20px
}
.rotated {
  transform: rotate(45deg);
  font-size: 16px;
}
.bgwhite {
  display: flex;
  flex-direction: column
}

.icons {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  flex-wrap: wrap
}
.content > div{
  margin: 20px 0
}
.content > div:last-of-type {
  margin: 50px 0
}

.slider {
  background-color: #dbe2e6;
  padding: 0;
  height: auto;
  overflow: hidden
}

.slider .slides {
  height: 150vh;
}
.slider2 .slides {
  height: 80vh;
}
.slider2 {
  padding: 0 15%;
  min-height: initial
}
.iconcontbanner {
  height: auto;
  background-color: #fff;
  overflow-x: hidden
}
.iconcontbanner .slidecont {
  display: flex;
  width: 100%;
  gap: 20px;
  height: 230px;
  margin: 50px 0;
  min-height: 350px;
  flex-direction: row
}
.iconcontbanner .slidecont:nth-of-type(odd) {
  flex-direction: row-reverse
}

.slidecont .slideimg {
  min-width: 50%;
  height: 100%;
  background-color: #fff;
  box-shadow: var(--light-shadow);
  width: 100%;
  border-radius: 10px;
  overflow: hidden;

}
.slideimg img {
  width: 100%;
  height: 100%;
}
.body .currentexhibition {
  padding: 0;
  height: auto;
  max-height: 100vh;
  background-color: #fff;
}

.currentexhibition img {
  width: 100%;
  height: 100%;
  object-fit: contain
}
.currentexhibition .mobile {
  display: none
}
.slidecont .iconcont {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 400px;
  border-radius: 10px;
  height: 200px;
  align-self: center
}

.iconcont .iconcon {
  position: absolute;
  top: -20px
}
.animationvideo {
  width: 100%;
  height: 100vh;

}

.fullheight {
  width: 100%;
  height: 100%
}
.animationvideo video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  background-color: #000
}

.articles {
  background-color: #fff;
  padding: 20px;
}
.articles h1 {
  text-align: center;
  margin: 50px 0;
  display: flex;
  flex-direction: column;

}
.articles .titlecont a small {
  margin-top: 0
}
.articles .titlecont a i{
  font-size: 17px;
  margin-left: 5px;

}
.titlecont h1 span {
  margin-bottom: 10px
}
.titlecont h1 span::after {
  content: '';
  width: 100px;
  height: 2px;
  position: absolute;
  bottom: -10px;
  left: 50%;
  background-color: var(--gray-text);
  transform: translateX(-50%);
  max-width: 80%;
}
.titlecont h1 small {
  font-size: 14px;
  color: var(--gray-text);
  margin-top: 10px
}
.articles .innerarticles {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center
}
.body .questionsbanner {
  padding: 0;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto
}
.questions {
  color: #fff;
  font-size: 30px;
  display: flex;
  flex-direction: column;
  gap: 100px;
}
.questiontitle {
  color: #fff;
  padding-top: 50px;
  display: flex;
  align-items: center;
}
.questiontitle .logo {
  display: flex;
  align-items: center;
}
.questiontitle #logo img {
  width: 145px;
  height: 60px
}
.questions i  {
  color: #fff;
  margin-right: 10px;
  font-size: 25px
}
.questionsbanner img {
  width: 100%;
  height: 100%;
  position: absolute;
}
.body .academyquestions {
  padding: 100px 20%;
  height: auto;
  color: #fff;
}
#root .body .academyslides {
  padding: 0
}
#root .body .academyslides .slide {
  padding: 40px 60px
}
#root .body .academyslides .slide h1 {
  text-align: left;
}
#root .body .academyslides .control-dots {
  justify-content: center
}
.academyslides .carousel-root .carousel:last-of-type {
  display: none
}
.questionslide {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center
}
.questionslide .logo {
  align-self: center;
  margin: 30px 0
}
.questionslide #logo img {
  width: 210px;
  height: 70px;
}
.linkcont {
  display: flex;
  justify-content: center
}
.questioncont {
  display: flex;
  flex-direction: column;
  gap: 30px;
  text-align: left
}
.split {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 40px
}
.split > div {
  width: 100%
}
.rightitem {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  gap: 20px
}
.questionlink {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px
}
.questioncont a {
  color: #fff;
  text-align: center;
  font-weight: bold;
}
.body .academybanners  .academyquestions {
  height: 100vh;
}
.displayimgs {
  display: flex;
  gap: 20px;
  margin-top: 40px
}
.displayimgs .displayimg {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.displayimg img {
  width: 100%;
  height: 100%;
  border-radius: 5px
}
.displayimg a {
  position: absolute;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(94, 94, 94, 0.479);
  border-radius: 5px;
  transition: all 0.3s;
  transform: scale(0.75);
  opacity: 0;
  font-weight: bold;
  font-size: 20px
}
.displayimg:hover a {
  transform: scale(1);
  opacity: 1;
}
.questionslide {
  height: 100%;
  width: 700px;
  max-width: 100%;
}
.questionslideimgs {
  width: 900px;
  max-width: 100%;
}
.academyquestions p {
  margin: 20px 0
}
.academyquestions::-webkit-scrollbar {
  width: 5px
}
.academygrad {
  height: 100%
}
.academyquestions img {
  width: 100%;
  height: 100%;
}
.academybanners {
  background: linear-gradient(47deg, rgba(0,169,255,1) 0%, rgba(78,87,244,1) 57%, rgba(178,180,186,1) 100%);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
.innerquestions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-top: 30px;
}
.opacityanimate {
  animation: fadein cubic-bezier(0.165, 0.84, 0.44, 1) 1s;
}
.innerquestionpopup * {
  animation: opacity cubic-bezier(0.165, 0.84, 0.44, 1) 1s;
}
.innerquestionpopup {
  padding: 20px
}
.widthanimator {
  height: 100%;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.822);
  border-radius: 5px;
  left: 0;
}
.innerquestionpopup .iconbox {
    background-color: transparent;
    box-shadow: none;
    width: 50px;
    height: 50px
}
.innerquestions .iconbox .iconcon {
  min-height: 50px;
  margin-bottom: 10px;
  background-color: transparent;
  box-shadow: none;
  justify-content: flex-start;
  font-size: 23px
}
#root .innerquestionpopup .iconbox .questionicon  {
  margin: 0;
  color: #000;
  filter: drop-shadow(0px 10px 5px rgba(0, 0, 0, 0.482))
}
@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadein {
  from {
      opacity:1;
      width: 0
  }
  to {
      opacity:0;
      width: 100%
  }
}
.questionpopup {
  background-color: rgba(255, 255, 255, 0.822);
  color: #000;
  border-radius: 5px;
  opacity: 0;
  transition: all 0.3s;
  top: 20px;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;

}
.activequestionmodal {
  pointer-events: initial;
  top: 0;
  opacity: 1;
}
.allquestions {
  color: #fff;
  display: flex;
  flex-direction: column;
  padding-bottom: 0;
  gap: 15px;
}
.innerquestions .questioncont  {
  display: flex;
  align-items: center;
  font-size: 17px;
  font-weight: lighter;
  color: rgb(233, 233, 233);
  cursor: pointer;
}

.innerquestions .questioncont:hover .themeicon, .innerquestions .activequestion .themeicon {
  margin-right: 10px;
  transform: rotate(-45deg);
  color: #fff
}
.innerquestions .questioncont:hover , .innerquestions .activequestion .questioncont {
  color: #fff
}
.sect {
  display: flex;
}
.rot {
  display: block;
}
.innerquestions i {
  font-size: inherit;
  color: #fff;
  margin-right: 15px;
  width: 15px
}
.underlinea {
  display: flex;
  flex-direction: column
}
.underlinespan {
  display: block
}
.underlinespan::after{
  position: absolute;
  bottom: -5px;
  content: '';
  width: 0%;
  height: 2px;
  background-color: #fff;
  left: 0;
  transition: all 0.3s
}
.underlinea:hover .underlinespan::after {
  width: 80%
}
.animationvideo a {
  left: 50%;
  z-index: 100;
  bottom: 30px;
  position: absolute;
}
