.animationvideo div {
  height: 1200vh
}
.animationvideo video {
  position: sticky;
  width: 100%;
  height: 100vh;
  top: 0;
} */ 
.body .sl {
  padding: 0;
  overflow: hidden;
  background-color: white;
  height: 120vh;
}
.home .articles {
  background-image: url(https://0xsociety.com/wp-content/uploads/2021/06/gradient-0xSociety-scaled.jpg);
  background-size: cover;
  background-position: center;
  min-height: 80vh
}
.home .articles .article {
  box-shadow: none;
  border: solid 1px #fff;
  color: #fff
}
.home .articles .article .graytext, 
.home .articles .article a, 
.home .articles .article a i,
.home .titlecont h1,
.home .titlecont h1 a small,
.home .titlecont h1 a i
{
  color: #fff
}
.home .titlecont h1 {
  color: #fff
}
.home .titlecont span::after {
  background-color: #fff
}
.bgwhite .animateddownarrow {
  position: absolute;
  bottom: 100px;
  left: 50%;
  transform: translateX(-50%)
}
.body .academybanner {
}
.videoplaceholder img {
  width: 100%;
  height: 100%;
}
.templatebanner  {
  position: sticky;
  top: 0
}
.themeicon {
  font-weight: lighter
}
.questionicon {
  margin-right: 5px
}
@media (max-width: 768px) {
   .templatebanner, .body .currentexhibition, .bgwhite, .animationvideo, .body .artistpage .banner  {
    position: relative;
    top: 0
  }

}
@media (max-width: 750px) {
  .iconcont .iconcon {
    top: -40px
  }
  .body .academyquestions {
    padding: 40px;
  }
}
