@media (max-width: 600px) {
  .iconcontbanner .slidecont, .iconcontbanner .slidecont:nth-of-type(odd) {
    flex-direction: column;
    min-height: 450px

  }
  .slidec > div {
    flex-direction: column-reverse;
    display: flex
  }
  .slider .slides {
    height: 1000px;
  }
  .slider .slidec h1 {
  }
  .slidec .circle img {
    width: 100%;
    height: 400px
  }

  .bgwhite h1 {
  }
  .allquestions {
    padding: 20px 0
  }
  .questionpopup p {
    font-size: 13px
  }
  .innerquestions {
    grid-template-columns: 2fr 3fr
  }
  .allquestions .sect, .allquestions .questioncont span {
    font-size: 13px
  }
  h1 {
    font-size: 28px
  }
}
@media (max-width: 540px) {
  .currentexhibition .wide {
    display: none
  }
  .currentexhibition .mobile {
    display: block
  }
}