.nextprevtab {
  height: 100%;
}
.slides {
  height: 100%;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  padding: 20px
}
.slidetab  {
  height: 100%;
  z-index: 111;
}
.slidec {
  display: flex;
  flex-direction: column;
  gap: 50px;
  height: 100%;
  justify-content: center;
  align-items: center;
}
.slidec img {
  width: 100%;
  z-index: 2;
  user-select: none;
}
.slidec > div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px
}
.slides .slidetab {
  width: 100%;
  left: 150%;
  position: absolute;
  z-index: 10;
  transition: all 0.5s;
  display: flex;
  flex-direction: column
}
.slides .lefttab {
  left: -150%
}


.slides .activeslidetab {
  left: 0
}

.slidesbtn, .slidecontrols {
  display: flex;
  font-size: 28px;
  left: 10%;
  gap: 10px
}
.slidesbtn {
  position: absolute;
  top: 50%;
  align-items: flex-end;
  justify-content: flex-end;
  gap: 20px;
  transform: translateY(-50%)
}
.bubbles {
  font-weight: bold;
}
.slidecontrols {
  position: relative;
  left: initial;
}
.slidesbtn strong {
  transform: rotate(-90deg);
  width: 20px;
  text-transform: uppercase;
}
.body   .carousel .control-dots {
  z-index: 100;
  justify-content: flex-start;
  padding: 10px 20px;
}

.body  .control-dots li{
  font-weight: bold;
  font-size: 25px;
  cursor: pointer;
  transition: all 0.3s;
  color: rgb(219, 219, 219)
}
.body  .control-dots .selectedbtn, .body  .control-dots li:hover {
  color: #fff;
  text-decoration: underline
}
.slidesbtn div {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}
.slidesbtn div span, .slidecontrols span {
  cursor: pointer;
  transition: all 0.3s
}

.slidesbtn div span:hover, .slidesbtn div .active, .slidecontrols .active, .slidecontrols span:hover {
  color: #fff
}
.slidec {
  padding: 20px;
}
.slidec .appbtn {
  width: 100%
}
.slidec .circle {
  position: absolute;
  width: 500px;
  height: 500px;
  top: -100px;
  left: 30px;
  z-index: -1;
  left: -100vh;
  transition: all 0.7s;
  transition-delay: 0.3s;
  opacity: 0;
  animation: animate 60.2s infinite;
  z-index: 1;
  pointer-events: none;
  filter: brightness(70%);
}
.slidec .imgskeltonload {
  border-radius: 50%
}

@keyframes animate {
  0% {
   top: -335px;
  }
  50% {
    top: 235px;

  }
  100% {
   top: -335px;
  }
}
.selected .slidec .circle {
  left: 0;
  opacity: 1;
  top: -40px
}

.slidec .img {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  padding: 20px;
  margin: auto 0;
  z-index: 2;
}
.slidec img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  pointer-events: none
}
.slidec h1 {
  width: 100%;
  text-align: center
}
.slidec > div {
  width: 70%
}
.slidec p {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  width: 100%;
  font-size: 24px;
  display: flex;
  margin: 0 auto;
}
.slidec div p {
  max-width: 400px;
  background-color: rgba(255, 255, 255, 0.726);
  padding: 10px;
  z-index: 3;

}
.slidetwo .innerslidetwo {
  display: flex;
  gap: 30px;
  flex-wrap: wrap
}
.slider2 h1 {
  margin: 40px 0;
  margin-bottom: 0;
  width: 400px;
  font-size: 45px
}
.slider2  .slidetab  {
  align-items: center;
  justify-content: center;
}
.slider2 .slidecont p:first-of-type {
    margin-bottom: 20px;
}
.slider2 .slidecont {
  font-weight: bold;
  width: 400px
}
.slider2 .slidecontrols span {
  display: flex;
  transition:  all 0.3s
}
.slider2 span section {
  border-bottom: solid 4px transparent;
  padding-bottom: 5px
}
.slider2 .active  section {
  border-bottom: solid 4px var(--second-color);
}
.slidetab .slidecontrols {
  display: flex;
  flex-direction: column;
  width: 250px;
  font-size: 33px
}
.artists .slide {
  background-size: cover;
  background-position: center
}
@media (max-width: 1600px) {
  .slidesbtn {
    left: 10px
  }
}
@media (max-width: 1000px) {

  .slidec p {
    font-size: 18px
  }

  .slidec img {

  }

  .slidec > div {
    display: flex;
    flex-direction: column-reverse;
    align-items: center
  }
}
@media (max-width: 800px) {
  .slidec img {

  }
}