.accessrow {
  display: grid;
  grid-template-columns:repeat(3, 250px);
  grid-template-rows: repeat(10, 50px);
  margin-top: 40px;
  justify-content: center;
  align-items: center;
  gap: 20px;
  overflow: hidden
}
.iconboxcontainer {
  height: 250px;
  grid-column: span 1;
}
.innericonboxcontainer {
  width: 100%;
  height: 100%;
  padding: 30px;
  transition: all 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: var(--light-shadow);
  border-radius: 5px;
  background: #fff

}
 .innericonboxcontainer:hover {
   background-color: var(--second-color);
   box-shadow: 0px 22px 36px -19px var(--second-color);
   transform: translateY(-10px)
  }
 .innericonboxcontainer:hover .iconcontbox , 
 .innericonboxcontainer:hover .iconcontbox i, 
 .innericonboxcontainer:hover .accesscont,
 .ibct:hover  {
  color: #fff;
  filter: none;
 }
 .accesscont {
   color: var(--gray-text);
   margin-top: 20px;
   display: block;
   text-align: center
 }
 .body .grantinfo.academyinfo .inneracademyinfo h2 {
   background-color: initial
 }
.iconboxcontainer .iconcontbox {
  color: var(--second-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  text-align: center
}
.iconcontbox i {
  color: var(--second-color);
  filter: var(--icon-shadow);
  font-size: 26px
}
.iconboxcontainer:nth-of-type(1) {
  grid-row: 1/5
}
.iconboxcontainer:nth-of-type(2) {
  grid-row: 2/6
}
.iconboxcontainer:nth-of-type(3) {
  grid-row: 5/10
} 
.iconboxcontainer:nth-of-type(4) {
  grid-row: 6/11
}
.iconboxcontainer:nth-of-type(5) {
  grid-row: 4/6;
}
@media (max-width: 900px) {
 .accessrow {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap
}
    .innericonboxcontainer {
      width: 250px;
      height: 250px
    }
}