.exhibitionpage {
  background-color: #f9f9f9;
  padding: 20px
}

.ttt {
  margin: 50px 0;
  text-align: center
}
.innerexhibitions {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center 
}
.innerexhibitions .exhibitioncont, .exhibitionskeleton {
  width: 200px;
  height: 300px;
  display: block
}
.exhibitioncont {
  cursor: pointer;
}
.exhibitionskeleton {
  background-color: #fff;
  border-radius: 5px;
  animation: skeleton 2.2s infinite
}
.modalcont {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  widows: 650px;
  max-width: 90%
}
.modalcont h2 {
  font-size: 27px;
  margin-bottom: 10px
}
.modalcont .exhibitionimg {
  width: 100%;
  height: 70vh;
  border-radius: 5px;
  overflow-y: hidden;
  overflow-x: auto
}
.exhibitionimg::-webkit-scrollbar-track {
  background-color: transparent
}
.modalcont .exhibitionimg img {
  width: 750px;
  height: 100%;
  object-fit: cover;
  background-color: transparent
}
.modaltext {
  width: 100%;
  color: #fff;
  margin-bottom: 5px
}
@keyframes skeleton {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.exhibitioncont img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}