.iconbox {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 150px;
  height: 150px;
  box-shadow: var(--light-shadow);
  border-radius: 5px;
  justify-content: center;
  gap: 10px;
  padding: 20px;
  text-align: center;
  background-color: #fff
}
 .iconcon {
  width: 50px;
  height: 50px;
  box-shadow: var(--light-shadow);
  display: flex;
  justify-content: center;
  align-items: center;;
  background-color: #fff;
  border-radius: 5px
}
.iconcont p {
  font-size: 13px
}
.iconcon i {
  filter: drop-shadow(0px 10px 5px rgba(1, 203, 152, 0.842));
  color: var(--second-color);
}
.iconcont {
  background-color: #f1f1f1;
}
.skillbox {
  width: 350px;
  max-width: 90%;
  display: flex;
  align-items: center;
  gap: 10px;
  box-shadow: var(--light-shadow)
}
.skillbox .iconbox {
  box-shadow: none;
  background-color: transparent
}
.skillbox .percent {
  width: 150px;
  display: flex;
  height: 13px;
  background-color: #fff;
  border-radius: 10px;
  margin-top: 10px
}
.percent .percentage {
  height: 100%;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--second-color);
  left: 0;
  border-radius: 10px
}
.skillbox {
  text-transform: capitalize
}
.skillbox span span {
  color: var(--second-color);
  font-size: 10px
}