.artists .artistsbanner {
  padding: 0;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center
}
.ourartist > h2 {
  margin: 30px 0;
  margin-top: 60px;
  text-align: center
}
.artistsbanner img,  .artistsbanner .imgskeltonload   {
  position: absolute;
  top: 0
}
.artistsbanner div {
  z-index: 1;
}
.artistscont {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 10px 10%;
  overflow: hidden
}
.artists .sl {
  height: 100vh
}
.artists .titlecont {
  z-index: 111;

  color: #fff
}
.artists .titlecont span::after {
  background-color: #fff
}
.artists .sl {
  background: var(--theme-gradient);
  background-size: cover;
  background-position: center
}
.artists div p {
  font-size: 13px
}
.slidec {
  margin: 20px 0
}

.body .artistquotes .control-dots .selectedbtn {
  color: var(--second-color)
}