.skeleton {
  box-shadow: var(--light-shadow);
  border-radius: 10px;
  overflow: hidden;
  animation: fadeInOut 1.4s  Infinite;

}
.skeleton.row {
  flex-direction: row;
  display: flex;
  width: 500px;
  height: 250px;
  gap: 10px;
  margin: 50px
}
.skeleton.row .skeletonimg {
  width: 250px;
  height: 250px;
  background-color: #eee
}
.skeleton.row .skeletoncont {
  width: 250px;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 0;
  padding-right: 10px
}

.skeleton.column {
  height: 450px;
  width: 300px;
  display: flex;
  flex-direction: column;
  gap: 10px
}
.skeleton.column .skeletonimg {
  width: 300px;
  height: 225px;
  background-color: #eee;
}
.skeleton.column .skeletoncont {
  width: 300px;
  height: 200px;
  padding: 10px
}
.skeleton h2 {
  width: 90%;
  height: 15px;
  background-color: #eee
}
.skeleton .skeletontext {
  height: 50px;
  width: 100%;
  background-color: #eee;
  margin-top:20px;
  display: block
}
.skeleton .skeletontext:last-of-type {
  height: 30px;
  width: 60%
}

@keyframes fadeInOut {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
