.artistcard {
  width: auto;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.artistcard:nth-of-type(odd) {
  flex-direction: row-reverse
}
.body .artistscont .artistcard img, .body .artistscont .artistcard .imgskeltonload  {
  width: 250px;
  max-height: 250px;
  min-height: 250px;
  border-radius: 50%
}

.artistcard .rightcard {
  width: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: space-between;
  align-content: space-between;
  height: 250px;
}
.rightcard button {
  width: 100%
}
.rightcard h2 {
  white-space: nowrap;
}
.rightcard h2 small {
  display: block;
  font-size: 14px;
  margin-top: 10px
}
.rightcard .graytext {
  max-width: 250px;
}
.rightcard .fa-quote-left {
  font-size: 28px;
  color: var(--second-color);
  margin-right: 10px
}
.artistcard:nth-of-type(odd) {
}
@media (max-width: 630px) {
  .artistscont {
    gap: 50px
  }
  .artistcard {
    flex-direction: column;
    height: auto;
  }
  .artistcard:nth-of-type(odd) {
    flex-direction: column
  }
  .artistcard .rightcard {
    justify-content: flex-start;
    gap: 15px
  }
}