.innercontainerrow > div:nth-last-of-type(odd){
    flex-direction: row-reverse
}
.innercontainerrow {
  display: flex;
  justify-content: center;
  align-items: center
}
.containerrow  {
  padding: 40px 10px;
  overflow: hidden;
}
.containerrow > h2 {
  text-align: center;
  margin: 40px 0
}
.containerrow .titlecont {
  margin: 40px 0;
}