.footer {
  padding: 40px 10%;
  background-color: #f9f9f9;
}

.footer .columns {
  display: flex;
  justify-content: space-around;
  gap: 25px
}
.column h4 {
  text-align: center
}
.rights {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  text-align: center;
}
.rights h5 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px
}
.socialicon {
  border-radius: 50%;
  width: 28px;
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center
}
.socialicons {
  display: flex;
  margin-top: 20px;
  justify-content: center
}
.socialicons i {
  margin: 0 10px;
  font-size: 15px;
}
.socialicon.fa-facebook {
  color:#4c69ba;
}
.socialicon.fa-facebook:hover {
  background-color: #4c69ba;
  color: #fff
}
.socialicon.fa-instagram {
  color: #cd486b;
}
.socialicon.fa-linkedin-in {
  color: #0072b1 
}
.socialicon.fa-linkedin-in:hover {
  color: #fff;
  background-color: #0072b1 
}
.socialicon.fa-instagram:hover {
  background-color: #cd486b;
  color:#fff
}
.socialicon.fa-twitter {
    color:  #00acee
}
.socialicon.fa-twitter:hover {
  color: #fff;
  background-color:  #00acee
}
.socialicon.fa-pinterest, .fa-youtube {
  color: #E60023
}
.socialicon.fa-pinterest:hover, .socialicon.fa-youtube:hover {
  color: #fff;
  background-color: #E60023
}
.footer a {
  cursor: pointer;
  white-space: nowrap
}
.footer .column:hover h3 a {
  text-decoration: initial;
  pointer-events: none;
}
.footer .column:hover span {
  color: initial
}
@media (max-width: 750px) {
  .footer {
    padding: 40px
  }
}