
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  height: 90px;
  align-items: center;
  padding: 0 10%;
  transition: all 0.3s
}
.scrollednav {
  background-color: rgba(255, 255, 255, 0.822);
  height: 80px;

}
.scrollednav .links > a {
  color: #000
}
.scrollednav .mobbtn hr {
  background-color: #000
}
.mobbtn {

  width: 35px;
  cursor: pointer;
  display: none
}
.mobbtn.open .l1 {
  transform: rotate(45deg);
  top: 8px;
}
.mobbtn.open .l2 {
  opacity: 0;
}
.mobbtn.open .l3 {
  width: 100%;
  transform: rotate(135deg);
  top: -10px;
}
.mobbtn hr {
  height: 2px;
  background: #f9f9f9;
  border-radius: 10px;
  transition: all 0.3s;
  margin: 7px;
  margin-left: 0;
} 
.mobbtn:hover hr {
  color: #fff
}
.mobbtn .l1 {
  width: 100%;
} 
.mobbtn .l2 {
  width: 70%;
}
.mobbtn .l3 {
  width: 40%;
}
.links > a{
  margin: 0 15px
}

.navmenu {
  position: fixed;
  background-color: #fff;
  width: 80%;
  height: 100%;
  left: 0;
  border-radius: 0 10px 10px 0;
  box-shadow: var(--light-shadow);
  top: 0;
  padding:20px;
  z-index: 1;
  transition: all 0.3s;
  left: -400vh;
  z-index: 1001;
}
.navmenu-enter {
  left: 0;
}
.navmenu .linksmenu {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  gap: 10px
}
.navmenu .linksmenu a {
  color: #000
}
.navmenu .column {
  padding-bottom: 10px;
  font-size: 14px
}
.navmenu .linksmenu span:hover {
  color: var(--theme-second)
} 

.navmenu .logo {
  left: -15px; 
} 
.navmenu   i {
  display: block;
  font-size: 14px
} 
.navmenu h3 {
  font-size: 16px;
}
.navmenu .linksmenu  a:hover{
  text-decoration: underline;
  color:  var(--second-color) 
}
.navmenu .linksmenu  .Mui-expanded h3 a, .navmenu .linksmenu  .Mui-expanded i  {
  color: var(--second-color)
}


.navmenu .column {
  margin: 10px 0
}
.navmenu .acor{
  border-bottom: solid 1px #ddd 
}
.linksmenu .MuiPaper-elevation1 {
  box-shadow: none
}
.navmenu .MuiAccordionDetails-root div {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 14px
}
.navmenu .MuiAccordion-root:before {
  display: none
}
.progressbar {
  width: 100%;
  height: 2px;
  background-color: var(--theme-color);
  position: absolute;
  bottom: 0;
  left: 0;
}
@media (max-width: 550px) {
    .links {
      display: none
    }
    .mobbtn {
      display: block
    }

}