.templatebannerpage {

}
.thb {
  height: 70vh;
  width: 100%;
}
.thb img, .thb .imgskeltonload {
  width: 100%;
  height: 100%;
  filter: brightness(70%);
  position: fixed;
  top: 0;
  left: 0;
  transition: all 1.3s;
  transition-delay: 1.1s
}
.templatebannerpage .titlecont {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
  z-index: 1;
  font-size: 21px
}
.templatebannerpage .titlecont span::after{
  background-color: #fff
} 
.templatebannerpage .titlecont small {
  color: #fff;
  font-size: 24px
}
@media (max-width: 769px) {
  .thb img {
    position: relative;
  }
}