.program {
  display: flex;
  height: 400px;
  max-width: 650px;
  margin: 0 auto
}
.program:nth-of-type(odd) {
  flex-direction: row-reverse
}
.program .programimg {
  width: 40%;
  height: 100%;
  overflow: hidden;
  border-radius: 5px
}
.programimg::before {
  width: 10px;
  height: 300%;
  content: '';
  position: absolute;
  background-color: #fff;
  display: block;
  top: -50%;
  z-index: 1;
  transform: rotate(33deg)
}
.programimg img {
  width: 100%;
  height: 100%;
  transition: all 0.3s
}
.programimg:hover img {
  transform: scale(1.04)
}
.programcont {
  width: 60%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center
}
.programcont button {
  width: 100%
}