.lostpage .banner {
  padding: 0
}
.lostpage img {
  width: 100%;
  height: 100%;
}

.youarelost p {
  user-select: none;
  color: #fff;
  font-size: 340px
} 
.lostcont {
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center
}
 .youarelost .appbtn {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  background-color: #fff;
  color: #000
}
.youarelost .appbtn:hover {
  background-color: #fff;
  color: #000
}
.youarelost button span{
  display: flex;
  flex-direction: row-reverse;
}
 .youarelost button span i {
  margin-right: 5px
}
.youarelost button:hover {
  background-color: var(--theme-color)
}

@media (max-width: 800px) {
  .youarelost p {
    font-size: 250px
  }
}
@media (max-width: 500px) {
  .youarelost p{
    font-size: 175px
  }
}
@media (max-width: 400px) {
  .youarelost p {
    font-size: 130px
  }
}