.body .programsbanner {
  min-height: 70vh;
  background-color: #f9f9f9;
  height: auto
}
.body .programsbanner img {
  width: 100%;
  height: 100%;
  left: 0;
}
.imgleft {
  display: flex
}
.leftimgs, .rightimgs {
  height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column
}
.programsbanner .leftimgs img, .programsbanner .rightimgs img   {
  width: 100%;
  height: 50%
}
.programsbanner .titlecont h1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 28px
} 
.programsbanner .titlecont small {
  font-size: 14px
}
.gridcont {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  gap: 20px;
  flex-direction: column;
  align-items: center
}
.gridcont .imgleft {
  width: 300px;
  height: 400px;
  overflow: hidden;
  box-shadow: var(--shadow);
  border-radius: 5px;
}
.imgleft img {
  width: 100%;
  height: 100%;
  transition: all 0.3s;

}
/* .imgleft img:hover {
  transform: scale(1.05)
} */

.iconscont {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
  min-width: 400px;
  max-width: 50%;
}
.body .iconscont .iconcon {
  top: -25px
}
.iconscont .contentbox {
  width: 160px;
  height: 160px;
  padding: 10px;
  font-size: 13px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  border-radius: 5px
}
.iconcont {
  transition: all 0.3s;
  max-width: 80%;
}
.programsbanner .iconscont .iconcont:hover {
  transform: scale(1.05)
}
.programsbanner .iconcont p {
  color: var(--gray-text)
}
.programsrow {
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin: 50px 0;
  padding: 10px
}
.programs .carousel-root {
  height: 100vh
}
.programs .carousel-root .slider, .programs .carousel-root .slide, .programs .carousel-root .slider-wrapper, .programslide  {
  height: 100%;
}
 .programslideimg, .programslideimg img {
  width: 100%;
  height: 100%;
  filter: brightness(60%)
} 

.programs .carousel-root .iconcont {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 5px;
  background-color: #fff
}
.programs .carousel-root .iconcont  .contentbox {
  width: 400px;
  max-width: 90%;
  padding: 20px;
}
.programs .carousel-root .iconcon {
  top: -60px
}
.programslide .titlecont {
  position: absolute;
  top: 15%;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  color: #fff
}
.programslide .titlecont small {
    color: #fff
}
.programslide .titlecont span::after {
  background-color: #fff
}
@media (max-width: 480px) {
  .programsrow .program {
    flex-direction: column;
    height: auto;
  }
  .programcont {
    width: 100%
  }
  .programsrow .program .programimg {
    width: 100%;
    height: 300px;
  }
  .programsrow .program .programimg::before {
    transform: rotate(50deg);
    height: 400%;
    top: -88%;
    left: -10%;
  }
}