.exhibtitioncard {
  display: flex;
  width: 750px;
  max-width: 90%;
  height: 250px;
  gap: 20px
}

.exhibtitioncard .exhibitionimg {
  width: 65%;
  height: 100%;
}
.exhibitionimg img {
  width: 100%;
  height: 100%;
}
.exhibtioncont {
  width: 50%;
  gap: 10px;
  overflow-y: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: safe center;

}
.exhibtioncont p {
  margin-top: 10px
}
.exhibtioncont::-webkit-scrollbar {
  width: 4px
}
.exhibitionlink:hover {
  text-decoration: underline
}
@media (max-width: 600px) {
  .body .exhibtitioncard, .body .exhibtitioncard.rowrevrse {
    flex-direction: column
  }
  .exhibtioncont {
    justify-content: flex-start
  }
  .exhibtitioncard  {
    width: 100%;
    height: 500px;
  }
  .exhibtitioncard .exhibitionimg {
    width: 100%
  }
  .exhibtitioncard .exhibtioncont {
    width: 100%
  }
}