.animateddownarrowdiv {
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: center;
  justify-content: center
}
.animateddownarrow .themeicon {
  font-size: 24px;
  color:  #fff;
  animation: animateArrow 2.2s infinite;
  transition: all 0.3s
}
.animateddownarrow .plate {
  width: 30px;
  height: 1px;
  background-color: #fff;
  display: block
}

@keyframes animateArrow {
  0% {
    transform: translateY(0)
  }
  50% {
    transform: translateY(5px)
  }
  100% {
    transform: translateY(0)
  }
}